const reg1 = /^1[0-9]{10}$/,
	reg2 = /^[1-9][0-9xX]{17}$/,
	reg3 = /^[0-9]{4}$/;
export default {
	data() {
		return {
			stop: false,
			sec: 0,
			telval: '',
			yzval: '',
			tel: '',
			bizcode: 13,
			requestUrl: this.$Store.state.requestUrl
		};
	},
	onLoad: function(options) {
		this.bizcode = options.bizcode;
		switch (getApp().globalData.provinceCode) {
			case 'D': //山东
				this.tel = '0532-66985957';
				break;
			case 'JL': //吉林
				this.tel = '15652287347';
				break;
			case 'LN':
				//辽宁
				this.tel = '024-31932190';
				break;
			case 'QPZJ': //浙江
				this.tel = '0571-28227057';
				break;
			case 'JS': //江苏
				this.tel = '15801152037';
				break;
			case 'AH': //安徽
				this.tel = '15801152037';
				break;
			case 'N': //湖南
				this.tel = '15801152037';
				break;
			case 'I': //江西
				this.tel = '15210353015';
				break;	
			default:
				this.tel = '';
				break
		}
	},
	onHide: function() {
		this.stop = true;
		this.sec = 0;
	},

	onUnload: function() {
		this.stop = true;
		this.sec = 0;
	},
	methods: {
		sub_message() {
			uni.showLoading({
				title: '提交中',
			})
			return new Promise((reslove, reject) => {
				uni.request({
					url: this.requestUrl + '/user/updateUserInfoMobile',
					method: "POST",
					data: {
						"openid": getApp().globalData.openid,
						"phonenum": this.telval,
						"captcha": this.yzval,
						"projectServerName":this.$Store.state.serverName
					},
					success: res => {
						uni.hideLoading()
						console.log('updateUserInfoMobile', res)
						reslove(res)
					},
					fail: err => {
						uni.hideLoading()
						reject('updateUserInfoMobile', err)
					}
				})
			})
		},
		getCaptcha() {
			uni.showLoading({
				title: '获取中',
			})
			return new Promise((reslove, reject) => {
				uni.request({
					url: this.requestUrl + '/user/getCaptcha',
					method: "POST",
					data: {
						"phonenum": this.telval,
						"sendtype": "1",
						"projectServerName":this.$Store.state.serverName
					},
					success: res => {
						uni.hideLoading()
						console.log('getCaptcha', res)
						reslove(res)
					},
					fail: err => {
						uni.hideLoading()
						reject('getCaptcha', err)
					}
				})
			})
		},
		inputYzval(e) { //验证码
			this.yzval = e.detail.value;
		},
		inputTelval(e) { //手机号
			this.telval = e.detail.value
		},
		tj() {
			if (!reg1.test(this.telval)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~',
					showCancel: false
				})
			} else if (!reg3.test(this.yzval)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的验证码！~',
					showCancel: false
				})
			} else {
				//调提交接口
				this.sub_message().then(res => {
					if (res.data.result.businessCode == 0) {
						uni.redirectTo({
							url: '../scan/scan?q=' + getApp().globalData.qrcode,
						})
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.result.msg,
							showCancel: false
						})
					}
				}).catch(err => {
					console.log(err)
				})
			}
		},
		getyz() {
			if (this.sec > 0) {
				return false;
			}
			if (!reg1.test(this.telval)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~',
					showCancel: false
				})
			} else {
				this.getCaptcha().then(res => {
					if (res.data.result.businessCode == 0) {
						this.sec = 60;
						this.timer();
					} else {
						uni.showModal({
							title: '提示',
							content: '验证码获取失败！请稍后再试并确认手机号是否输入正确',
						})
					}
				}).catch(err => {

				})
			}
		},
		timer() {
			let timer = null;
			let that = this;
			if (this.stop || this.sec <= 0) {
				this.sec = 0
				return false;
			}
			this.sec -= 1
			timer = setTimeout(() => {
				this.timer()
			}, 1000);
		}
	}
}
